    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 20px 20px rgba(0, 0, 0, 0.3);
    z-index: -999;
}

.brand{
    text-align: center;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #084343;
    font-size: 60px;
    font-weight: 300;
    margin: 0px;
    padding: 0px;
}

.support{
    text-align: center;
}

.signin{
    /* position: relative; */
    /* left: 40%; */
    /* border-radius: 20px; */
    width: 300px;
    margin: 10px;
    padding: 10px;
    /* background-color: #d8f3dc; */

    align-self: center;
}

.pillar {
    height: 100%;
    width: 600px;
    background-color: #d8f3dc;
    border: 1px solid black;

    display: flex;
    flex-direction: column;
    justify-content: center;
}

.quote {
    margin-top: 10px;
    width: 80%;

    align-self: center;

    text-align: center;
}

.register {
    margin-top: 20px;
}


@media (max-width: 620px){
    .pillar {
        width: 100%;
    }

    .signin {
        width: 48%;
    }
}