.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .modal-main {
    position:fixed;
    background: white;
    width: 40%;
    height: auto;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
  }
  .heading{
      text-align: center;
  }
  .account{
      margin: 15px;
      padding: 15px;
  }
  .profile{
      border-radius: 15px;
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }